<template>
  <div>
    <div v-if="status_message" class="text-right mb-3">
      <span :class="`badge badge-soft-${failure ? 'danger' : 'success'}`">{{ status_message }}</span>
    </div>
    <VueFileAgent
      ref="vueFileAgent"
      id="dropzone"
      class="dropzone dropzone-single mb-3"
      :meta="meta"
      :accept="extensions"
      :uploadUrl="url"
      v-model="fileRecords"
      :maxFiles="maxFiles"
      :maxSize="maxSize"
      :linkable="linkable"
      :editable="editable"
      :deletable="deletable"
      @beforedelete="(deleteFileRecord = $event), $bvModal.show(`delete_attachment_${project_id}`)"
      @rename="editFile($event)"
      @upload="onUpload($event)"
      @upload:error="onUploadError($event)"
      @upload:update="onUploadUpdate($event)"
      @select="fileSelected($event)"
      :uploadHeaders="uploadHeaders"
      :helpText="help_text"
      :compact="compact"
    >
    </VueFileAgent>
    <b-overlay :show="isBusy" no-wrap opacity="0.5"></b-overlay>
    <b-modal :id="`delete_attachment_${project_id}`" title="Delete Attachment" size="sm" hide-header hide-footer body-class="text-center">
      <h3 class="mb-4 mt-3">Are you sure?</h3>
      <p class="text-muted px-3">This action shall permanently remove this item. This cannot be undone!</p>
      <div class="mx`-4 mb-3 pt-2">
        <b-button size="sm" variant="secondary" @click="$bvModal.hide(`delete_attachment_${project_id}`)">Cancel</b-button>
        <b-button size="sm" class="ml-3" variant="primary" @click="beforeDelete()">Confirm</b-button>
      </div>
    </b-modal>
  </div>
</template>

<script>
  //const axios = require("axios");
  import VueFileAgentPlugin from "vue-file-agent";
  const VueFileAgent = VueFileAgentPlugin.VueFileAgent;
  import "vue-file-agent/dist/vue-file-agent.css";
  import FileUtil from "../js/core/file-manager";

  export default {
    name: "FileUpload",
    components: {
      VueFileAgent,
    },
    props: {
      fileRecords: {
        type: Array,
        default: () => [],
      },
      maxFiles: {
        type: Number,
        default: () => 1,
      },
      maxSize: {
        type: String,
        default: () => "5MB",
      },
      extensions: String,
      url: String,
      app: String,
      linkable: Boolean,
      editable: Boolean,
      deletable: Boolean,
      project_id: Number,
      meta: {
        type: Boolean,
        default: () => true,
      },
      compact: {
        type: Boolean,
        default: () => true,
      },
      file_name: String,
      help_text: {
        type: String,
        default: "Drop files here to upload",
      },
    },
    data() {
      return {
        isBusy: false,
        uploadHeaders: {},
        deleteFileRecord: {},
        status_message: "",
        failure: false,
      };
    },
    mounted: function() {},
    methods: {
      thumbUrl(file) {
        return file.myThumbUrlProperty;
      },
      onFileChange(file) {
        // Handle files like:
        this.fileUploaded = file;
      },
      onUpload(event) {
        this.isBusy = false;
        console.log("uploaded", event);
        this.$emit("uploaded", event);
      },
      onUploadError() {
        this.isBusy = false;
      },
      onUploadUpdate(event) {
        this.isBusy = false;
        console.log("upload update", event);
      },
      async fileSelected(event) {
        var validFileRecords = event.filter((fileRecord) => !fileRecord.error);
        if (!validFileRecords?.[0]?.name()) {
          // If selected file type is invalid, remove from the array.
          this.fileRecords.pop();
          this.display_status_message(true, "file_invalidtype_helptext", { fileSize: this.maxSize });
          return;
        }
        var file_name;
        if (this.file_name) {
          file_name = this.file_name;
        } else {
          file_name = validFileRecords[0].name();
          file_name = file_name.replace(/[_!@#$%^&*()]/g, "");
          file_name = file_name.replace(/[^a-zA-Z0-9]/g, "-");
          file_name = file_name.replaceAll("-" + validFileRecords[0].ext, "." + validFileRecords[0].ext);
          file_name = file_name.toLowerCase();
        }

        let data = await FileUtil.getSignedLink(this.app, "post-object", file_name, this.extensions, "public-read");
        this.$refs.vueFileAgent
          .upload(data.url, {}, validFileRecords, function createFormData(fileData) {
            //this passes all the presigned url key and values as post params and posts it to the S3 url along with the file object.
            var formData = new FormData();
            let appendData = data.fields;
            for (var field in appendData) {
              formData.append(field, appendData[field]);
            }
            formData.append("file", fileData.file);
            return formData;
          })
          .then(
            function(result) {
              // Form s3 download url
              console.log("uploded: ", data.fields, result);
              this.$emit("downloadUrl", `${data.url}${data.fields.key}`, event, (response) => {
                this.display_status_message(response ? false : true, response ? "file_upload_helptext" : "file_upload_failed_helptext");
              });
            }.bind(this),
          );
      },
      editFile(fileRecord) {
        this.$emit("edit_file", fileRecord, (response) => {
          this.display_status_message(response ? false : true, response ? "file_edit_helptext" : "file_edit_failed_helptext");
        });
      },
      async beforeDelete() {
        let s3_delete_response = await FileUtil.delete(this.app, this.deleteFileRecord.name());
        if (s3_delete_response && s3_delete_response.status == 200) {
          this.$emit("delete_file", this.deleteFileRecord, (response) => {
            this.display_status_message(response ? false : true, response ? "file_delete_helptext" : "file_delete_failed_helptext");
          });
        } else {
          //s3 delete fail
          this.display_status_message(false, "file_delete_failed_helptext");
        }
        this.$refs.vueFileAgent.deleteFileRecord(this.deleteFileRecord);
        this.$bvModal.hide(`delete_attachment_${this.project_id}`);
      },
      display_status_message(response, message, dynamicValue) {
        this.failure = response;
        (this.status_message = `components.${message}`), dynamicValue ? dynamicValue : "";
        setTimeout(() => {
          this.status_message = "";
        }, 1500);
      },
    },
  };
</script>
