<template>
  <div>
    <FileUpload ref="fileupload" app="forms" :maxFiles="maxFiles" :maxSize="maxSize" :extensions="extensions" @downloadUrl="formBackgroundImage" />
    <b-img v-if="value" :src="value" fluid alt="Responsive image"></b-img>
  </div>
</template>
<script>
  import FileUpload from "@/components/FileUpload";
  import formAPI from "@/js/forms/form";
  export default {
    name: "FormUpload",
    components: { FileUpload },
    props: {
      value: {
        type: String,
      },
      type: {
        type: String,
      },
      maxFiles: {
        type: String || Number,
      },
      maxSize: {
        type: String,
      },
      extensions: String,
      label: String,
    },
    data() {
      return {
        email: "",
        form_id: "",
      };
    },
    async created() {
      this.getFormDetails();
    },
    methods: {
      async getFormDetails() {
        this.email = atob(this.$route.params.email);
        this.form_id = this.$route.params.form_id;
        let result = await formAPI.getData(this.form_id, this.email);
        this.maxFiles = JSON.parse(result[0].form_json).items[0].children[0].children[0].props.maxfiles;
      },
      // After s3 upload these event is emitted
      formBackgroundImage(url) {
        this.$root.$emit("uploaded_files", { [this.label]: url });
      },
    },
  };
</script>
